@import '../../../main.scss';
.mainContainer {
  margin-left: auto;
  margin-right: auto;
  max-width: 375px;
  min-width: 375px;

  padding-left: 16px;
  padding-right: 16px;
  @include tablet {
    max-width: 768px;
    min-width: 768px;

    padding-left: 32px;
    padding-right: 32px;
  }
  @include desktop {
    max-width: 1440px;
    min-width: 1440px;

    padding-left: 100px;
    padding-right: 100px;
  }
}
.search_bar {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.searchLookingWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;
}

.searchLookingWrapper > p {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: #000000;
  opacity: 0.5;
  margin-top: 24px;
}

.searchLookingWrapper > img {
  width: 300px;
  height: 200px;
  margin-top: 50px;
}
